<template lang="html">
  <div class="toggle-bg" @click="toggle" :active="value"></div>
</template>

<script>
export default {
  name:'tiggle',
  props:{
    id:{
      default:()=>{return ''}
    },
    value:{
      default:()=>{return false}
    }
  },
  methods:{
    toggle(){
      this.$emit('switch', {value:this.value, id:this.id})
    }
  }
}
</script>

<style lang="scss" scoped>
  .toggle-bg{
    position: relative;

    width: 60px;
    height: 34px;

    padding: 3px;
    background: grey;
    border-radius: 17px;

    cursor: pointer;
    transition: 0.3s;

    &::before{
      content:"";
      position: absolute;
      left: 3;

      height: 28px;
      width: 28px;

      border-radius: 15px;

      background: white;
      transition: 0.3s;
    }
  }

  .toggle-bg[active=true]{

    background: green;

    &::before{
      transform: translateX(26px);
    }
  }




</style>
