<template>
  <section>
    <div class="content">
      <h1>Doors</h1>
      <div class="grid-view">
        <card v-for="(door,d) in doors" :key="d">
          <template v-slot:header>{{door.name}}</template>
          <template v-slot:toolbox><toggle :id="d" @switch="toggleDoor" :value="door.open"></toggle></template>

          <template v-slot:content>
            {{door.id}}
            <br>
            last opened:
            <br>
            {{door.history[0].time}}

          </template>

        </card>



      </div>
    </div>
  </section>
</template>

<script type="text/javascript">

import card from '@/components/card'
import toggle from '@/components/toggle'


export default{
  components:{
    card, toggle
  },
  data(){
    return{
      doors:[
        { name:'Door 1', id:1231231, open:true, history:[{user:123, time:'10PM'}] },
        { name:'Door 1', id:1231231, open:true, history:[{user:123, time:'10PM'}] }


      ]
    }
  },
  methods:{
    toggleDoor(door){
      this.doors[door.id].open = !door.value
    }
  }

}
</script>
